@import url(https://fonts.googleapis.com/css2?family=Poppins&display=swap);
*{margin:0;padding:0;box-sizing:border-box}body{background-image:linear-gradient(to left, #ff3838, #fff200);color:#fff;font-family:"Poppins", sans-serif}.app{display:flex;align-items:center;flex-direction:column;margin:100px auto;width:40%;height:70vh}.app header{display:flex;justify-content:center;flex-direction:column}.app header h1{font-size:2rem;text-transform:uppercase;text-align:center;font-weight:bolder}.app header form{background-color:antiquewhite;width:100%;display:flex;justify-content:center}.app header form input,.app header form button{padding:1rem;font-size:2rem;outline:none;border:0;background-color:transparent;transition:all 0.3s ease}.app header form button{color:#ff3838;cursor:pointer;transition:all 0.3s ease}.app header form button:hover{color:antiquewhite;background-color:#ff3838}.app ul{height:60vh}.app ul .todo-list{margin:1rem;width:500px;background-color:#fff;color:#000;display:flex;font-size:2rem;justify-content:space-between}.app ul .todo-list.checked{transition:1s;opacity:0.5;text-decoration:line-through}.app ul .todo-list.fall{transition:1s ease-out;transform:translateY(10rem) rotateZ(40deg);opacity:0}.app ul .todo-list li,.app ul .todo-list .trash,.app ul .todo-list .check{list-style:none;padding:0.5rem}.app ul .todo-list .btn{outline:none;border:0;color:white;cursor:pointer;padding:0.5rem 0.7rem}.app ul .todo-list .btn .pointernone{pointer-events:none}.app ul .todo-list .trash{background-color:#ff3838;transition:all 0.5s ease}.app ul .todo-list .trash:hover{color:#ff3838;background-color:#fff}.app ul .todo-list .check{background-color:#009432;transition:all 0.5s ease}.app ul .todo-list .check:hover{color:#009432;background-color:#fff}

