@import url("https://fonts.googleapis.com/css2?family=Poppins&display=swap");
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
body {
  background-image: linear-gradient(to left, #ff3838, #fff200);
  color: #fff;
  font-family: "Poppins", sans-serif;
}
.app {
  display: flex;
  align-items: center;
  flex-direction: column;
  margin: 100px auto;
  width: 40%;
  height: 70vh;
  header {
    display: flex;
    justify-content: center;
    flex-direction: column;
    h1 {
      font-size: 2rem;
      text-transform: uppercase;
      text-align: center;
      font-weight: bolder;
    }
    form {
      background-color: antiquewhite;
      width: 100%;
      display: flex;
      justify-content: center;
      input,
      button {
        padding: 1rem;
        font-size: 2rem;
        outline: none;
        border: 0;
        background-color: transparent;
        transition: all 0.3s ease;
      }
      button {
        color: #ff3838;
        cursor: pointer;
        transition: all 0.3s ease;
        &:hover {
          color: antiquewhite;
          background-color: #ff3838;
        }
      }
    }
  }
  ul {
    height: 60vh;
    .todo-list {
      margin: 1rem;
      width: 500px;
      background-color: #fff;
      color: #000;
      display: flex;
      font-size: 2rem;
      justify-content: space-between;
      &.checked {
        transition: 1s;
        opacity: 0.5;
        text-decoration: line-through;
      }
      &.fall {
        transition: 1s ease-out;
        transform: translateY(10rem) rotateZ(40deg);
        opacity: 0;
      }
      li,
      .trash,
      .check {
        list-style: none;
        padding: 0.5rem;
      }
      .btn {
        outline: none;
        border: 0;
        color: white;
        cursor: pointer;
        padding: 0.5rem 0.7rem;
        .pointernone {
          pointer-events: none;
        }
      }
      .trash {
        background-color: #ff3838;
        transition: all 0.5s ease;
        &:hover {
          color: #ff3838;
          background-color: #fff;
        }
      }
      .check {
        background-color: #009432;
        transition: all 0.5s ease;
        &:hover {
          color: #009432;
          background-color: #fff;
        }
      }
    }
  }
}
